@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
.button {
  color: #ffffff;
  background-color: #68b738;
  border: none;
  font-family: "Oswald", "Arial", sans-serif;
  text-transform: uppercase;
  text-decoration: none;
}
.button--secondary {
  color: #444444;
  background-color: #f2f2f2;
}
.card-row__heading {
  color: #222222;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1;
  text-transform: uppercase;
}
.card-row__list {
  color: #444444;
  line-height: 1;
}
.card__heading {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
}
.card__list {
  color: #444444;
  font-size: 12px;
}
.catalog-additional {
  padding: 20px;
}
.catalog-additional__heading {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 24px;
}
.catalog-additional__list {
  list-style: none;
}
.catalog-list {
  padding: 20px;
}
.catalog-sections__item {
  padding: 20px;
  background-color: #f2f2f2;
}
.catalog-sections__heading {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  font-weight: 400;
  text-transform: uppercase;
}
.catalog-sections__text {
  color: #444444;
}
.catalog-sections__link {
  color: #000000;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  text-transform: uppercase;
  text-decoration: none;
}
.contacts {
  padding: 20px;
}
.contacts__heading {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
}
.contacts__address {
  color: #444444;
  line-height: 20px;
}
.control {
  color: #222222;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  text-transform: uppercase;
}
.developer {
  color: #444444;
  font-size: 16px;
  text-decoration: none;
}
.example {
  padding: 20px;
  background-color: #ebebeb;
}
.example__text {
  color: #444444;
}
.example__list {
  text-align: center;
}
.example__title {
  color: #444444;
  font-size: 12px;
  line-height: 1;
  text-transform: lowercase;
}
.example__definition {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  line-height: 1;
  text-transform: uppercase;
}
.example__spending {
  font-family: "Oswald", "Arial", sans-serif;
  line-height: 20px;
  text-transform: uppercase;
}
.field-group {
  padding: 20px 0;
  border: none;
}
.field-group__heading {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 26px;
}
.field-group__list {
  padding: 0;
  list-style: none;
}
.field {
  color: #444444;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  text-transform: uppercase;
}
.field__input {
  font-family: inherit;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
}
.form {
  padding: 20px;
}
.form__group {
  margin: 0;
}
.offer {
  padding: 40px;
  color: #ffffff;
  background-color: #68b738;
  background-image: url("../img/background/catbag.jpg");
  font-size: 16px;
  text-align: center;
  line-height: 20px;
}
.page-footer {
  padding: 20px;
  background-color: #f2f2f2;
}
.page-main__heading {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 40px;
}
.page-main__subheading {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  text-transform: uppercase;
}
.page {
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
}
.page__body {
  margin: 0;
}
.process {
  padding: 20px;
}
.process__list {
  color: #444444;
  list-style: none;
}
.promo {
  padding: 20px;
  color: #ffffff;
  background-color: #68b738;
  background-image: url("../img/background/promo.jpg");
  text-align: center;
}
.promo__heading {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 1;
}
.promo__text {
  font-family: "Oswald", "Arial", sans-serif;
  line-height: 1;
  text-transform: uppercase;
}
.show-more__heading {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
}
.show-more__text {
  color: #444444;
  font-size: 12px;
  line-height: 16px;
}
.site-menu {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 20px;
  line-height: 1;
  text-align: center;
  text-transform: uppercase;
  list-style: none;
}
.site-menu__link {
  color: #000000;
  text-decoration: none;
}
.switcher__button {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
